<template>
    <div class="loginContainer">
        <head-top >
            <div slot="calender" class="calender" @click="changeCalender">{{nowday}}</div>
        </head-top>
        
        <section class="item" v-for="record of this.records" :key="record.id">
            <div class="title" v-if="record.get('action')">
                <div class="point" :class="{change_color: record.get('action')}"></div>
                <p class="default_color">{{"上班打卡时间" + record.get('timeString')}}</p>
            </div>
            <div class="title" v-else>
                <div class="point" :class="{change_color: true}"></div>
                <p class="default_color">{{"下班打卡时间" + record.get('timeString')}}</p>
            </div>
        </section>
        <section class="item" v-if="this.displayDard">
            <div class="title">
                <div class="point"></div>
                <p>{{this.action?"上班打卡":"下班打卡"}}</p>
            </div>
            <div class='circle' @click="dcard">
                 <div class='text'>{{this.action?"上班打卡":"下班打卡"}}</div>
                 <div class='nowtime'>{{nowtime}}</div>
            </div>
        </section>

        <foot-guide></foot-guide>
        <alert-tip v-if="showAlert" :showHide="showAlert" @closeTip="closeTip" :alertText="alertText"></alert-tip>
        <inline-calendar  
                     v-show="showCalendar"
                     @change="choiceCalendar"
                     mode="single"
        />
    </div>
</template>

<script>
    import footGuide from '@/components/footer/footGuide'
    import headTop from '../../components/header/yhead'
    import alertTip from '../../components/common/alertTip'
    import {formatDate,formatDay,pySort} from '@/config/mUtils'

    import {mapState, mapMutations} from 'vuex'
    import {dCard, getCardQuery,updateIncome} from '../../service/getData'

    export default {
        data(){
            return {
                showCalendar:false,//
                showAlert: false, //显示提示组件
                alertText: null, //提示的内容
                nowtime:"获取时间中..",
                nowday:"",
                records:[],
                action:true,
                displayDard:true
            }
        },
        mounted(){
            let _this = this;
             this.timer = setInterval(function () {
                    // _this.time = new Date();    //修改数据date
                    _this.nowtime = formatDate(new Date())
                },1000);
            this.nowday = formatDay(new Date()) 
            this.updateView()
            pySort()
        },
        updated(){
            if(!this.login){
                console.log('not login back to login')
                this.$router.push('/login');
            }
        },
        components: {
            headTop,
            alertTip,
            footGuide,
        },
        computed: {
            ...mapState([
            'userId',
            'login'
            ]),
            //判断手机号码
            rightPhoneNumber: function (){
                return /^1\d{10}$/gi.test(this.phoneNumber)
            }
        },
        methods: {
            ...mapMutations([
                'RECORD_USERINFO',
            ]),
            //改变登录方式
            choiceCalendar(date){
                console.log('change to ' + date.format('YYYY-MM-DD'));
                this.showCalendar = false;
                this.nowday = date.format('YYYY-MM-DD');
                this.updateView(this.nowday)
            },
            changeCalender(){
                this.showCalendar = !this.showCalendar;
            },
            closeTip(){
                this.showAlert = false;
            },
            async updateView(dataString){
                let results = await getCardQuery(dataString).find()
                console.log('updateView result')
                console.log(results)
                this.records = results
                if(results.length %2 == 1){
                    this.action = false
                }else{
                    this.action = true
                }
                if(!results.length){
                    this.displayDard = true;
                }
                if(results.length >= 2){
                    this.displayDard = false
                }
                for (let i = 0; i < results.length; i++) {
                    var object = results[i];
                    // console.log(object.id + ' - ' + object.get('action'));
                }
                //test
                updateIncome();
            },
            dcard(){
                let card = dCard()
                card.spawn({
                    'action':this.action,
                    'timeString':formatDate(new Date())
                })
                let _this = this;
                card.save()
                .then((card) => {
                    // Execute any logic that should take place after the object is saved.
                    alert('打卡成功');
                    _this.updateView();
                }, (error) => {
                    // Execute any logic that should take place if the save fails.
                    // error is a Parse.Error with an error code and message.
                    alert('出错了，请截图这个页面联系管理员' + error.message);
                });

            }

        }
    }

</script>

